.bgn-parallax {
    .react-parallax {
        margin: 0 auto;
        width: 650px;
        height: 100%;
    }
    .react-parallax-content{
        display: flex;
        justify-content: center;
        align-items: center;
        height: inherit;
    }
    .react-parallax-bgimage{
        bottom: -19%;
    }
}
@media screen and (max-width: 600px) {
    .bgn-parallax .react-parallax {
        width: 100%;
        display: flex;
        justify-content: center;
    }
}